export class InlineGroup {
    ops;
    constructor(ops) {
        this.ops = ops;
    }
}
export class VideoItem {
    op;
    constructor(op) {
        this.op = op;
    }
}
export class BlotBlock {
    op;
    constructor(op) {
        this.op = op;
    }
}
export class BlockGroup {
    op;
    ops;
    constructor(op, ops) {
        this.op = op;
        this.ops = ops;
    }
}
export class ListGroup {
    items;
    constructor(items) {
        this.items = items;
    }
}
export class ListItem {
    item;
    innerList;
    constructor(item, innerList = null) {
        this.item = item;
        this.innerList = innerList;
    }
}
export class TableGroup {
    rows;
    constructor(rows) {
        this.rows = rows;
    }
}
export class TableRow {
    cells;
    constructor(cells) {
        this.cells = cells;
    }
}
export class TableCell {
    item;
    constructor(item) {
        this.item = item;
    }
}
